import React from "react";
import Img from "gatsby-image";
import { useStaticQuery, graphql } from "gatsby";
import { useMappedImages } from "../../hooks/images";

export default function Labels({ onGalleryImageClick }) {
  const data = useStaticQuery(graphql`
    query {
      allFile(
        filter: {
          relativePath: {
            in: [
              "projects/beer-packaging/label-main.png"
              "projects/beer-packaging/label-neck-cap.png"
            ]
          }
        }
      ) {
        ...FluidImagesFragment
      }
    }
  `);
  const optimizedImages = useMappedImages(data.allFile);

  return (
    <div className="overflow-hidden max-w-3xl m-auto">
      <div className="flex flex-wrap -mx-2">
        <div
          className={"w-full px-2 py-2 sm:w-1/2"}
          onClick={() => {
            onGalleryImageClick("projects/beer-packaging/label-main.png");
          }}
        >
          <Img
            className={"cursor-zoom-in"}
            fluid={
              optimizedImages["projects/beer-packaging/label-main.png"]
                .childImageSharp.fluid
            }
          />
        </div>
        <div
          className={"w-full px-2 py-2 sm:w-1/2"}
          onClick={() => {
            onGalleryImageClick("projects/beer-packaging/label-neck-cap.png");
          }}
        >
          <Img
            className={"cursor-zoom-in"}
            fluid={
              optimizedImages["projects/beer-packaging/label-neck-cap.png"]
                .childImageSharp.fluid
            }
          />
        </div>
      </div>
    </div>
  );
}
